// import {useState} from 'react';
import { useContext, useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import Nav from 'react-bootstrap/Nav';
import { Tab } from 'bootstrap';
import { addItem } from '../store.js';


import {Context1} from './../App.js'
import { useDispatch } from 'react-redux';
 
function Deatil(props){

    // let {재고} = useContext(Context1)
    
    let [count , setCount] = useState(0)
    let {id}=useParams();
    let product=props.shoes.find(function(x){return x.id == id });
    let [alert,setAlert]=useState(true)
    let [탭, 탭변경] = useState(0)
    let dispatch=useDispatch()

    useEffect(()=>{

        let 꺼낸거 = localStorage.getItem('watched')
        꺼낸거 = JSON.parse(꺼낸거)
        꺼낸거.push(product.id)
        꺼낸거 = new Set(꺼낸거)
        꺼낸거 = Array.from(꺼낸거)
        localStorage.setItem('watched', JSON.stringify(꺼낸거))
    },[])
    

    useEffect(()=>{
        let a = setTimeout(()=>{setAlert(true)}) //스위치

        return ()=>{
            clearTimeout(a)    //기존 타이머 제거
                                //기존 데이터요청은 제거
        }
    },[])
    
    /* 
    
    useEffect(()=>{}) 1. 재렌더링마다 코드실행
    useEffect(()=>{}.[]) 2. mount시 1회 코드실행 
    useEffect(()=>{
        return()=>{
            3. unmount 1회 코드실행
        }
    }.[]) 
    
    */




    return(
        <div className="container">{
            alert==true ? <div className='alert alert-warning'>2초이내 구매시 할인</div>:null}
            <div className="row">
                <div className="col-md-6">
                    <img src={'https://codingapple1.github.io/shop/shoes'+(product.id+1)+'.jpg'} width="80%" />
                </div>
                <div className="col-md-4 mt-4">
                    <h4 className="pt-5">{product.title}</h4>
                    <p>{product.content}</p>
                    <p>{product.price}원</p>
                    <button className="btn btn-danger" onClick={()=>{
                        dispatch(addItem({id:product.id,name: product.title ,count : 1}))
                    }}>주문하기</button>
                </div>
            </div>
            
            <Nav variant="tabs"  defaultActiveKey="link0">
                <Nav.Item>
                    <Nav.Link onClick={()=>{탭변경(0)}} eventKey="link0">버튼0</Nav.Link>
                </Nav.Item>
                <Nav.Item>
                    <Nav.Link onClick={()=>{탭변경(1)}} eventKey="link1">버튼1</Nav.Link>
                </Nav.Item>
                <Nav.Item>
                    <Nav.Link onClick={()=>{탭변경(2)}}eventKey="link2">버튼2</Nav.Link>
                </Nav.Item>
            </Nav>
            <TabContent shoes={props.shoes} 탭={탭}/>

        </div>
    )
}
 
function TabContent({탭}){
//     if(탭 == 0){
//         return <div>내용0</div>
//     }else if(탭 == 1){
//         return <div>내용1</div>
//     }else if(탭== 2){
//         return <div>내용2</div>
//     }
    let [fade,setFade]=useState()
    // let {재고} = useContext(Context1)
    
    useEffect(()=>{
        setTimeout(()=>{setFade('end')},100)
        
        return ()=>{
            setFade('') 
        }
    },[탭])

    return (<div className={'start '+fade}>
        { [<div>재고</div>,<div>내용1</div>,<div>내용2</div>][탭]}
        </div>)
}

export default Deatil;